<template>

    <div class="bgc">
      <div class="title">
        <img src="../assets/title.png" alt="">
      </div>
      <div class="contain">
        <div class="left">
          <div class="logo">
            <div>
              <img v-if="ismsg" src="../assets/people.png" alt="">
              <img v-if="!ismsg" :src="img" alt="">
            </div>
            <span>{{phone}}</span>
          </div>

          <router-link :class="tabflag == 'preliminary' ? 'tab' : ''" :to="{name:'preliminary'}">我的初赛</router-link>

          <router-link v-if="isshow" :class="tabflag == 'playoff' ? 'tab' : ''" :to="{name:'playoff',params:{performance:grade,recsname:recsname}}">我的复赛</router-link>
        </div>
        <div class="right">
          <!--<router-view></router-view>-->
          <transition mode="out-in">
            <router-view></router-view>
          </transition>
        </div>
      </div>

    </div>
</template>

<script>

    import store from '../vuex/store'

    export default {
        name: "index",
        data(){
          return {
            phone:'',
            tabflag:'',
            playoff:true,
            grade:'11112222',//成绩,
            isshow:false,
            ismsg:true,
            img:'',
            mdbile:'',
            recsname:''
          }
        },
        methods:{

        },
        created(){
          if(this.$route.path=='/index/preliminary'){
            this.tabflag = 'preliminary'
          }else if(this.$route.path=='/index/playoff' || this.$route.path=='/index/playoff2' ) {
            this.tabflag = 'playoff'
          }
          console.log(this.tabflag);
          // debugger
          // console.log(this.$route.params.isshow);//是否显示复赛

          if(this.$route.params.isshow == '0'){
            this.isshow = false
          }
          if(this.$route.params.isshow == '1'){
            this.isshow = true
            this.recsname = this.$route.params.recsname
          }

          this.phone= this.$route.params.mdbile
          if(this.$route.params.avatarimg){
            this.ismsg = false
            this.img= this.$route.params.avatarimg
          }
        },
        updated(){
          console.log(this.$route.path);
          if(this.$route.path=='/index/preliminary'){
            this.tabflag = 'preliminary'
          }else if(this.$route.path=='/index/playoff' || this.$route.path=='/index/playoff2' ) {
            this.tabflag = 'playoff'
          }
          console.log(this.tabflag);
        },
        store
    }
</script>

<style scoped lang="scss">
  .v-enter,
  .v-leave-to {
    opacity: 0;
  }

  .v-enter-active,
  .v-leave-active {
    transition: all 0.3s ease;
  }

  .bgc{
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff9f5;
    .title{
      width: 100%;
      height: 45px;
      img{
        height: 100%;
      }
    }
    .contain{
      width: 960px;
      height: 100%;
      display: flex;
      .left{
        width: 217px;
        height: 260px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: #fff;
        .logo{
          height: 163px;
          width: 100%;
          display: flex;
          flex-direction: column;
          margin-top: 30px;
          align-items: center;
          border-bottom: 1px solid #eeeeee;
          div{
            width: 95px;
            height: 95px;
            border-radius: 100%;
            margin-bottom: 10px;
            overflow: hidden;
            img{
              width: 95px;
              height: 95px;
            }
          }
        }
        a{
          height: 13%;
          line-height: 34px;
        }
      }
      .right{
        width: 722px;
        margin-left: 20px;
        background-color: #fff;
      }

    }
  }
  .tab{
    color: #f76c1a;
    position: relative;
    &:after{
      content: '';
      width: 3px;
      height: 22px;
      position: absolute;
      left: -83px;
      top: 3px;
      background-color: #f76c1a;
    }
  }
</style>



